<template>
  <div class="chat-box" style="background-image: linear-gradient(to bottom right,#FC4668,#3F5EFB);" >
    <div class="message-list">
      <div v-for="message in messageList" :key="message.id" class="message">
        <div
            :class="['message-content', { isRobot: message.isRobot }]"
            v-html="message.content"
        ></div>
      </div>
    </div>
    <div class="message-input">
      <input v-model="question" @keyup.enter="sendMessage" placeholder="请输入您的问题" />
      <button @click="sendMessage">发送</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      question: "",
      messageList: [],
      robotReplies: [
        {
          keywords: ["我很焦虑", "我很不安", "我很害怕", "我很担心"],
          reply: "听到您描述这些情绪，我可以理解您此刻的心情。您可以尝试一些放松的方法，如深呼吸、冥想、休息等。",
        },
        {
          keywords: ["我好抑郁", "我好沮丧", "我好悲伤"],
          reply: "我可以理解您此刻的感受，心情不好是很正常的，不要对自己太苛求。您可以尝试找到一些自己感兴趣的事情来转移注意力。",
        },
        {
          keywords: ["我好孤独", "我好无助", "我好失落"],
          reply: "感觉孤独和失落是非常难受的，您可以尝试联系一些亲朋好友，或者加入一些社交活动，扩大自己的社交圈子。",
        },
        {
          keywords: ["我恨我的家庭", "我真的有亲情吗", "我讨厌我的妻子"],
          reply: "家庭和亲情关系是非常重要的，有时候沟通不畅也会导致一些问题。您可以尝试与家人进行沟通，多倾听对方的想法。",
        },
        {
          keywords: ["我不想工作", "我不想学习", "我讨厌职场"],
          reply: "工作和学习是每个人都必须面对的问题，有时候会遇到困难和挫折。您可以尝试与同事或者老师进行沟通，或者寻求一些帮助。",
        },
        {
          keywords: ["我感觉我不健康", "我好像得了病", "医疗对我的治疗真的有用吗"],
          reply: "健康问题是我们生活中必须面对的问题。如果您有任何疑问或者担心，可以尝试咨询医生或者专业人士。",
        },
        {
          keywords: ["其他"],
          reply: "我理解您的困惑，不过这个问题可能需要更多的信息和背景才能够给您提供更准确的回答。",
        },
        {
          keywords: ["谢谢你，我感觉好多了"],
          reply: "没关系，这是我应该做的",
        },
      ],
      currentId: 0,
    };
  },
  methods: {
    sendMessage() {
      const question = this.question.trim();
      if (!question) {
        return;
      }
      this.messageList.push({
        id: ++this.currentId,
        isRobot: false,
        content: "YOU:"+question,
      });

      const robotReply = this.getRobotReply(question);
      setTimeout(() => {
        this.messageList.push({
          id: ++this.currentId,
          isRobot: true,
          content: "AI:"+robotReply,
        });
      }, 1000);

      this.question = "";
    },
    getRobotReply(question) {
      for (let i = 0; i < this.robotReplies.length; i++) {
        const { keywords, reply } = this.robotReplies[i];
        for (let j = 0; j < keywords.length; j++) {
          if (question.indexOf(keywords[j]) !== -1) {
            return reply;
          }
        }
      }
      return "抱歉，我不太理解您的问题，可以请您换个问法吗？";
    },
  },
};
</script>

<style>
.chat-box {
  display: flex;
  flex-direction: column;
  width: 1500px;
  height: 700px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.message-list {
  flex: 1;
  overflow-y: auto;
}

.message {
  margin-bottom: 10px;
}

.message-content {
  display: inline-block;
  padding: 8px 12px;
  border-radius: 5px;
  max-width: 70%;
}

.message-content.isRobot {
  background-color: #f2f2f2;
}

.message-input {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.message-input input {
  flex: 1;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 8px;
  margin-right: 10px;
  height: 50px;
}

.message-input button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
}
</style>

